<template>
  <div class="main-wrapper">
    <form class="main-wrapper__form">
      <CustomTabs
        :tabs="customTabs"
        @getDataTabs="getDataTabs"
        :dataNeeded="true"
        ref="validation1"
      />
      <template>
        <CustomInput
          v-show="selectedTab === 'Wi-Fi'"
          :placeholderText="'Network name (SSID)'"
          :defaultErrorText="'Network name (SSID) is required'"
          :formField="'ssid'"
          :formPlace="['network', 'wifi']"
          :inputName="'ssid'"
          :required="selectedTab === 'Wi-Fi'"
          @getData="getData"
          ref="validation2"
        />
        <SearchSelect
          v-show="selectedTab === 'Wi-Fi'"
          :optionsCount="authentificationMethods"
          :search="false"
          :defaultText="'Authentification'"
          :defaultErrorText="'Authentification is required'"
          :form-place="['network', 'wifi']"
          :formField="'Authentification'"
          :required="selectedTab === 'Wi-Fi'"
          @getData="getData"
          ref="validation3"
        />
        <CustomInput
          v-show="
            form.network.wifi.Authentification === 'Basic (WPA2 Personal)' &&
            selectedTab === 'Wi-Fi'
          "
          :required="
            form.network.wifi.Authentification === 'Basic (WPA2 Personal)' &&
            selectedTab === 'Wi-Fi'
          "
          :placeholderText="'Password'"
          :defaultErrorText="'Password is required'"
          :formField="'password'"
          :hidden="true"
          :formPlace="['network', 'wifi']"
          :inputName="'Password'"
          @getData="getData"
          ref="validation4"
        />
        <SearchSelect
          v-show="
            form.network.wifi.Authentification ===
              'Enterprise (WPA2 Enterprise)' && selectedTab === 'Wi-Fi'
          "
          :required="
            form.network.wifi.Authentification ===
              'Enterprise (WPA2 Enterprise)' && selectedTab === 'Wi-Fi'
          "
          :optionsCount="security"
          :search="false"
          :defaultText="'Security'"
          :default-value="{ name: 'TLS' }"
          :defaultErrorText="'Security is required'"
          :formField="'mode'"
          :form-place="['network', 'wifi', 'enterprise']"
          @getData="getData"
          ref="validation5"
        />
        <template ref="TLS">
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'CA Certificates'"
            :defaultErrorText="'CA certificates is required'"
            :file="true"
            :form-field="'ca_cert'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'ca certificates'"
            @getData="getData"
            ref="validation6"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Idenity'"
            :defaultErrorText="'User idenity is required'"
            :formField="'identity'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'user identity'"
            @getData="getData"
            ref="validation7"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Private Key'"
            :defaultErrorText="'User private key is required'"
            :file="true"
            :formField="'private_key'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'private key'"
            @getData="getData"
            ref="validation8"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Private Key Password'"
            :defaultErrorText="'User private key password is required'"
            :formField="'private_key_password'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'private key password'"
            @getData="getData"
            ref="validation9"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'TLS' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Certificate'"
            :defaultErrorText="'User certificate is required'"
            :file="true"
            :formField="'client_cert'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'client certificate'"
            @getData="getData"
            ref="validation10"
          />
        </template>
        <template ref="PEAP">
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'CA Certificates'"
            :defaultErrorText="'CA certificates is required'"
            :file="true"
            :form-field="'ca_cert'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'ca certificates'"
            @getData="getData"
            ref="validation11"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Idenity'"
            :defaultErrorText="'User idenity is required'"
            :formField="'identity'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'user identity'"
            @getData="getData"
            ref="validation12"
          />
          <CustomInput
            v-show="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :required="
              form.network.wifi.enterprise.mode == 'PEAP' &&
              form.network.wifi.Authentification ==
                'Enterprise (WPA2 Enterprise)' &&
              selectedTab === 'Wi-Fi'
            "
            :placeholderText="'User Password'"
            :defaultErrorText="'User password is required'"
            :hidden="true"
            :formField="'password'"
            :formPlace="['network', 'wifi', 'enterprise']"
            :input-name="'user password'"
            @getData="getData"
            ref="validation13"
          />
        </template>
      </template>
      <SearchSelect
        :optionsCount="timezone"
        :search="true"
        :defaultValue="{ name: guestedTimezone, value: guestedTimezone }"
        :defaultText="'Time zone'"
        :form-field="'timezone'"
        :defaultErrorText="'Time zone is required'"
        @getData="getData"
        ref="validation14"
      />
      <CustomCheckbox
        v-show="selectedTab === 'Wi-Fi'"
        :placeholderText="'Hidden network'"
        :formField="'hidden'"
        :formPlace="['network', 'wifi']"
        @getData="getData"
        ref="customCheckbox"
      />
      <AdvancedSettings ref="advancedSettings" />
    </form>
  </div>
</template>

<script>
import CustomInput from '@/components/form/CustomInput.vue'
import CustomTabs from '@/components/form/CustomTabs.vue'
import SearchSelect from '@/components/form/SearchSelect.vue'
import CustomCheckbox from '@/components/form/CustomCheckbox.vue'
import AdvancedSettings from '@/components/settings/network/AdvancedSettings.vue'
import moment from 'moment-timezone'

export default {
  name: 'Network',
  components: {
    CustomInput,
    CustomTabs,
    SearchSelect,
    AdvancedSettings,
    CustomCheckbox,
  },
  data() {
    return {
      form: {
        version: '1.0.0',
        network: {
          wifi: {
            ssid: '',
            hidden: false,
            password: '',
            Authentification: '',
            enterprise: {
              mode: '',
              identity: '',
              private_key_password: '',
              ca_cert: '',
              private_key: '',
              client_cert: '',
            },
            dns: [],
            ipv4: {
              method: '',
              gateway: '',
              address: '',
            },
          },
          ethernet: {
            dns: [],
            ipv4: {
              method: '',
              gateway: '',
              address: '',
            },
          },
        },
        proxy: {
          server: {
            address: '',
            port: Number,
          },
        },
        ntp: [],
        trust_certificates: [],
      },
      selectedTab: '',
      customTabs: ['Wi-Fi', 'Ethernet'],
      authentificationMethods: [
        {
          name: 'None',
        },
        {
          name: 'Basic (WPA2 Personal)',
        },
        {
          name: 'Enterprise (WPA2 Enterprise)',
        },
      ],
      security: [
        {
          name: 'TLS',
        },
        {
          name: 'PEAP',
        },
      ],
      timezone: [],
      guestedTimezone: '',
      readyJSON: {},
    }
  },
  props: {
    settings: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    settings() {
      if (this.settings) {
        this.$refs.validation1.activateTabs()
        console.log('activate')
      }
    },
  },

  methods: {
    getData(formPlace, formField, selectedValue) {
      if (formPlace) {
        let formObj = this.form
        for (let i = 0; i < formPlace.length; i++) {
          formObj = formObj[formPlace[i]]
        }
        if (typeof selectedValue === 'boolean') {
          formObj[formField] = selectedValue
        } else {
          formObj[formField] = selectedValue.trim()
        }
        return
      }
      if (typeof selectedValue === 'boolean') {
        this.form[formField] = selectedValue
      } else {
        this.form[formField] = selectedValue.trim()
      }
    },
    getDataTabs(tab) {
      if (tab) {
        this.selectedTab = tab.textContent.trim()
      }
    },
  },
  mounted() {
    this.guestedTimezone = moment.tz.guess()
    this.timezone = moment.tz.names().map((name) => ({ name }))
  },
}
</script>

<style lang="scss" scoped>
@function rem($px) {
  @return ($px / 16px) + rem;
}

.error-message {
  color: #d42b2b;
  font-size: rem(13px);
  line-height: rem(16px);
  margin-top: rem(5px);
  position: relative;
  margin-left: rem(18px);
  display: flex;
  align-items: center;
  flex-direction: row;
  &::before {
    position: absolute;
    content: url('@/assets/img/warning.svg');
    font-size: rem(15px);
    line-height: rem(18px);
    font-weight: 500;
    position: absolute;
    width: 100%;
    height: 100%;
    left: rem(-18px);
    color: #d42b2b;
    align-items: center;
    flex-direction: row;
  }
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  // max-width: rem(735px);
  width: 100%;
  .main-wrapper__form {
    // background: rgba(255, 255, 255, 1);
    // border-radius: rem(30px);
    display: flex;
    // padding: rem(60px);
    flex-direction: column;
    justify-content: center;
    // max-width: rem(735px);
    gap: rem(17px);
    width: 100%;
    .main-btn {
      font-size: rem(19px);
      line-height: rem(23px);
      font-weight: 700;
      color: rgba(255, 255, 255, 1);
      background: rgba(0, 113, 226, 1);
      border-radius: rem(999px);
      padding: rem(14px) rem(17px) rem(15px) rem(17px);
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      text-align: center;
      cursor: pointer;
      transition: all 0.3s ease;
      width: 100%;
      border: 1px solid transparent;
      &:hover {
        opacity: 0.8;
      }
    }
    .preview-btn {
      color: rgba(134, 134, 139, 1);
      font-size: rem(19px);
      line-height: rem(29px);
      font-weight: 500;
      display: flex;
      flex-direction: row;
      gap: rem(12px);
      align-items: center;
      justify-content: center;
      text-align: center;
      cursor: pointer;
      transition: all 0.3s ease;
      &:hover {
        opacity: 0.8;
      }
      &::after {
        content: url(@/assets/img/diagonal-arrow.svg);
      }
    }
  }
}

@media (max-width: 768px) {
  .main-wrapper {
    .main-wrapper__form {
      padding: rem(20px);
      .main-btn {
        font-size: rem(19px);
        line-height: rem(23px);
      }
    }
  }
}
</style>
